.resume {
  display: flex;
  align-items: center;
  flex-direction: column;
  border: 2px solid #ddd;
  min-width: 50%;
  min-height: 75%;
  padding: 1.5rem;
  margin: 0.5rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.resume .preview {
  padding: 10px;
  width: 100px;
  max-width: 100%;
}
.resume .delete {
  background-color: #e11d48;
}
.resume .delete svg {
  fill: white;
}
.resume .edit {
  background-color: #075985;
}
.resume .edit svg {
  fill: white;
}
.resume .section {
  width: 100%;
  padding: 0.5rem;
}
.resume .section .heading {
  border-bottom: 2px solid #ddd;
  text-transform: uppercase;
  text-overflow: ellipsis;
  overflow: hidden;
}
.resume .section .content-container {
  padding-top: 0.25rem;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.resume .section .content-container .item {
  display: flex;
  justify-content: space-between;
}
.resume .section .content-container .item .main-info {
  overflow-wrap: anywhere;
}
.resume .section .content-container .item .main-info span {
  font-size: 1rem;
}
.resume .section .content-container .item .side-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-left: auto;
  padding-right: 10px;
  overflow-wrap: anywhere;
}
.resume .section .content-container .item .options {
  display: flex;
  align-items: flex-start;
  gap: 5px;
}
.resume .section .content-container .item .options button {
  display: flex;
  align-items: center;
}
.resume .section .content-container ul {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.resume .section .content-container ul .item {
  flex-wrap: wrap;
}
.resume .section .content-container form input,
.resume .section .content-container form textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid rgb(220, 220, 220);
  border-radius: 4px;
}
.resume .section .content-container form input:focus,
.resume .section .content-container form textarea:focus {
  border-color: #3b82f6;
  outline: none;
}
.resume .section .content-container form label {
  text-overflow: ellipsis;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 8px;
}/*# sourceMappingURL=resume.css.map */