.personal-info {
  display: flex;
  justify-content: center;
}
.personal-info .content-container {
  width: 100%;
}
.personal-info .item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.personal-info .item .contact {
  display: flex;
  justify-content: center;
  gap: 15px;
  flex-wrap: wrap;
}
.personal-info .item .contact div {
  display: flex;
  gap: 5px;
}
.personal-info .item .contact div h3 {
  overflow-wrap: anywhere;
}/*# sourceMappingURL=personal-info.css.map */